@import "../../index.scss";
.gpt3__footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: $color-footer;
  .gpt3__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
    h1 {
      font-family: "Manrope";
      font-weight: 800;
      font-size: 62px;
      line-height: 75px;
      letter-spacing: -0.04em;
    }
  }
  .gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    padding: 1rem;
    margin-bottom: 10rem;

    border: 1px solid #ffffff;
    cursor: pointer;
    p {
      font-family: $font-family;
      font-size: 18px;
      line-height: 21px;
      color: #ffffff;
      word-spacing: 2px;
    }
  }
  .gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;

    width: 100%;
    text-align: left;
    div {
      width: 250px;
      margin: 1rem;
    }
    .gpt3__footer-links_logo {
      display: flex;
      flex-direction: column;
      img {
        width: 118px;
        height: 30px;

        margin-bottom: 1rem;
      }
      p {
        font-family: $font-family;
        font-size: 12px;
        line-height: 14px;
        color: #ffffff;
      }
    }
    .gpt3__footer-links_div {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      h4 {
        font-family: $font-family;
        font-size: 14px;
        line-height: 16px;
        color: #ffffff;
      }
      p {
        font-family: $font-family;
        font-size: 12px;
        line-height: 14px;
        color: #ffffff;

        margin: 0.5rem 0;
        cursor: pointer;
      }
    }
    .gpt3__footer-copyright {
      margin-top: 2rem;
      text-align: center;
      width: 100%;
      p {
        font-family: $font-family;
        font-size: 12px;
        line-height: 14px;
        color: #ffffff;
      }
    }
  }
}
@media screen and (max-width: 992px) {
  .gpt3__footer-heading {
    h1 {
      font-size: 44px !important;
      line-height: 50px !important;
    }
  }
}
@media screen and (max-width: 576px) {
  .gpt3__footer-heading {
    h1 {
      font-size: 34px !important;
      line-height: 42px !important;
    }
  }
  .gpt3__footer-links div {
    margin: 1rem 0 !important;
  }
  .gpt3__footer-btn p {
    font-size: 14px !important;
    line-height: 20px !important;
  }
}
